import * as eva from '@eva-design/eva';
import { ApplicationProvider, Card, Modal, List, ListItem, Divider, Layout, Text, Button, Input, Icon } from '@ui-kitten/components';
import { SafeAreaView } from 'react-native';
import { Ionicons } from '@expo/vector-icons';
import { StyleSheet, View } from 'react-native';
import React, { useEffect } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Feather } from '@expo/vector-icons';

import { AddOrderCl } from './components/modals/AddOrder/index';
import { FixedContent } from './components/modals/FixedContent';
import { ModalizeProvider } from './components/ModalizeProvider';




export default function Home({ navigation }) {
  const modal = [];

  const [data, setData] = React.useState([]);

  const getData = async () => {
    try {
      const jsonValue = await AsyncStorage.getItem('@Orders');
      const Data = JSON.parse(jsonValue);
      const arr = Object.keys(Data).map((key) => Data[key]);
      setData(arr)
    } catch (e) {
    }
  }
  const removeCode = async (value) => {
    try {
      delete data[value];
      const results = data.filter(element => {
        return element !== undefined;
      });
      await AsyncStorage.setItem('@Orders', JSON.stringify(results))
    } catch (e) {
    }
  }

  useEffect(() => {
    const interval = setInterval(() => {
      getData()
    }, 500);
    return () => clearInterval(interval);
  }, []);
  const renderList = ({ item, index }) => (
    <ListItem
      title={`${item?.code}`}
      description={`${item?.tag}`}
      onPress={() => navigation.navigate('Order',{
        code: `${item?.code}`
      })}
      accessoryRight={<Feather
        onPress={() => removeCode(index)}
        name="trash"
        size={24}
        color="black"
      />}
    />
  );
  return (
    <ApplicationProvider {...eva} theme={eva.light}>
      <Layout style={{ width: '100%', height: '100%' }}>
        <ModalizeProvider>
          <SafeAreaView />

          <Layout style={styles.container}>
            <Text category='h3'>TKOrders 📦</Text>
            <Ionicons style={{ position: 'relative' }} onPress={() => modal[2].openModal()} name="ios-add-sharp" size={31} color="#666666" />
          </Layout>

          <List
            style={styles.containerL}
            data={data}
            ItemSeparatorComponent={Divider}
            renderItem={renderList}
          />
          <FixedContent
            ref={el => {
              modal[1] = el;
            }}
          />
          <AddOrderCl ref={el => {
            modal[2] = el;
          }}></AddOrderCl>
        </ModalizeProvider>
      </Layout>
    </ApplicationProvider>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    width: '100%',
    justifyContent: 'space-between',
    padding: 5
  },
  containerL: {
    marginTop: 15,
    backgroundColor: '#fff'
  },
});