import React, { useEffect } from 'react';
import { StyleSheet, Text, View, TouchableOpacity } from 'react-native';
import { Input } from '@ui-kitten/components';
import AsyncStorage from '@react-native-async-storage/async-storage';


export default function AddOrder() {
  const [code, setCode] = React.useState('');
  const [tag, setTag] = React.useState('');
  const [data, setData] = React.useState([])

  const AddOrder = async () => {
    const lastKeyOBJ = Object.keys(data).length + 1
    data[lastKeyOBJ] = { code: code, tag: tag };
    const arr = Object.keys(data).map((key) => data[key]);
    const results = arr.filter(element => {
      return element !== undefined;
    });
    storeData(results)
  }
  const storeData = async (value) => {
    try {
      const jsonValue = JSON.stringify(value)
      await AsyncStorage.setItem('@Orders', jsonValue)
    } catch (e) {
    }
  }

  const getData = async () => {
    try {
      const jsonValue = await AsyncStorage.getItem('@Orders')
      console.log(jsonValue)
      if (jsonValue == null) {
        console.log('null is detect')
        await AsyncStorage.setItem('@Orders', JSON.stringify({}))
      } else {
        console.log('done! \n isset AsyncStorage key')
        setData(JSON.parse(jsonValue))
      }
    } catch (e) {
    }
  }
  useEffect(() => {
    const interval = setInterval(() => {
      getData()
    }, 990);
    return () => clearInterval(interval);
  }, []);

  const clearAll = async () => {
    try {
      await AsyncStorage.clear()
    } catch (e) {
    }
    console.log('Done.')
  }

  return (
    <View style={s.content}>
      <Text style={s.content__heading}>Adicione um pacote 📦 </Text>
      <Input
        style={s.content__input}
        label='Código de rastreio'
        
        placeholderTextColor="#A4A4A4"
        placeholder="H5367HBR"
        onChangeText={nextValue => setCode(nextValue.toUpperCase())}
      />

      <Input
        label={'Etiqueta'}
        style={s.content__input}
        placeholderTextColor="#A4A4A4"
        placeholder="Capinha"
        onChangeText={nextValue => setTag(nextValue)}
      />

      <TouchableOpacity onPress={() => AddOrder()} style={s.content__button} activeOpacity={0.9} >
        <Text style={s.content__buttonText}>{'Adicionar'.toUpperCase()}</Text>
      </TouchableOpacity>
    </View>
  )
}
const s = StyleSheet.create({
  content: {
    padding: 20,
  },
  content__icon: {
    width: 32,
    height: 32,
    marginBottom: 20,
  },

  content__subheading: {
    marginBottom: 2,
    fontSize: 16,
    fontWeight: '600',
    color: '#ccc',
  },

  content__heading: {
    fontSize: 24,
    fontWeight: '600',
    color: '#333',
    marginBottom: 12
  },

  content__description: {
    paddingTop: 10,
    paddingBottom: 10,
    fontSize: 15,
    fontWeight: '200',
    lineHeight: 22,
    color: '#666',
  },

  content__input: {
    paddingVertical: 5,
    width: '100%',
    borderWidth: 1,
    borderColor: 'transparent',
    backgroundColor: 'transparent',
    borderBottomColor: '#808080',
    borderRadius: 1
  },

  content__button: {
    paddingVertical: 15,
    marginTop: 18,
    width: '100%',
    backgroundColor: '#333',
    borderRadius: 6,
  },

  content__buttonText: {
    color: '#fff',
    fontSize: 15,
    fontWeight: '600',
    textAlign: 'center',
  },
});
