import axios from "axios";
import * as eva from '@eva-design/eva';
import AsyncStorage from '@react-native-async-storage/async-storage';

import React, { useEffect } from "react";
import { ApplicationProvider, Card, Modal, List, ListItem, Divider, Layout, Icon } from '@ui-kitten/components';
import { View } from "react-native";
import { StyleSheet } from 'react-native';
import { SafeAreaView, FlatList, StatusBar } from 'react-native';

export default function Order({ navigation, route }) {
    const [data, setData] = React.useState()

    const { code } = route.params;

    async function getData() {
        try {
            const response = await axios.get('https://q7481t.deta.dev/', {
                params: {
                    trackingCode: code
                }
            });
            console.log(response.data);
            if (response.data.error != undefined) {
                alert(response.data.error);
                navigation.navigate('Home');
            } else {
                //console.log(response.data)
                const data = response.data
                setData(Object.keys(data).map((key) => data[key]))
            }
        } catch (error) {
            console.error(error);
            alert('internet error')
            navigation.navigate('Home');
        }
    }

    function getDataA() {
        axios.get('https://q7481t.deta.dev/', {
            params:{
                trackingCode: code
            }
        })
            .then(function (response) {
                console.log(response.data);
                if (response.data.error != undefined) {
                    alert(response.data.error);
                    navigation.navigate('Home');
                } else {
                    //console.log(response.data)
                    const data = response.data
                    setData(Object.keys(data).map((key) => data[key]))
                }
            })
            .catch(function (error) {
                console.error(error);
            alert(error)
            navigation.navigate('Home');
            })
    }
    useEffect(() => {
        getDataA()
        console.log('done')
    }, [])

    const renderList = ({ item, index }) => (
        <ListItem
            title={`${item.unidadeDestino == undefined & item.unidade.tipo == "País" ?
                'Origem do objeto: ' + item.unidade.nome :
                `Objeto recebido na ${item?.unidade.tipo} de ${item?.unidade.endereco.cidade}-${item?.unidade.endereco.uf} 📦`}`}
            description={`${item.unidadeDestino != undefined ?
                `🚚 Enviado para ${item.unidadeDestino.tipo} de ${item.unidadeDestino.endereco.cidade}-${item?.unidadeDestino.endereco.uf}`
                : `${item.descricao}`}`}
            disabled
        />
    );

    return (
        <ApplicationProvider {...eva} theme={eva.light}>
            <List
                style={styles.container}
                data={data}
                ItemSeparatorComponent={Divider}

                renderItem={renderList}
            />
        </ApplicationProvider>
    )
}

const styles = StyleSheet.create({
    container: {
        width: '100%',
        height: '100%',
        backgroundColor: '#fff'
    },
});